
       
document.addEventListener('DOMContentLoaded', function() {
    const dateElement = document.getElementById('current-date');
    const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
    const today = new Date().toLocaleDateString('en-US', options);
    dateElement.textContent = today;

let currentPage = 1;
const apiKey = 'E2PSJrDTKfUu2Ph9zGMFSC1OAAAA4iRd'; 

const apiUrlBase = `https://api.nytimes.com/svc/search/v2/articlesearch.json?q=technology&api-key=${apiKey}`;
const mostPopularUrl = `https://api.nytimes.com/svc/mostpopular/v2/viewed/1.json?api-key=${apiKey}`;
const topStoriesUrl = `https://api.nytimes.com/svc/topstories/v2/home.json?api-key=${apiKey}`;

const articlesLink = document.getElementById('articles-link');
const mostPopularLink = document.getElementById('most-popular-link');
const topStoriesLink = document.getElementById('top-stories-link');

const newsContent = document.getElementById('news-content');

function fetchArticles(page = 1) {
    const apiUrl = `${apiUrlBase}&page=${page}`;
    const loadingElement = document.getElementById('loading');
    
    loadingElement.style.display = 'block';

    fetch(apiUrl)
        .then(response => {
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            return response.json();
        })
        .then(data => {
            const articles = data.response.docs;
            const articlesContainer = document.getElementById('technology-news');
            
            articlesContainer.innerHTML = ''; 

            articles.forEach(article => {
                const imgSrc = article.multimedia && article.multimedia.length > 0 
                    ? 'https://www.nytimes.com/' + article.multimedia[0].url 
                    : 'https://via.placeholder.com/800x400'; 
                    
                const articleElement = document.createElement('article');
                articleElement.innerHTML = `
                    <img src="${imgSrc}" alt="News Image">
                    <h2>${article.headline.main}</h2>
                    <p>${article.snippet}</p>
                    <a href="${article.web_url}" class="read-more">Read More....</a>
                `;
                articlesContainer.appendChild(articleElement);
            });

            loadingElement.style.display = 'none'; 
        })
        .catch(error => {
            console.error('Error fetching articles:', error);
            loadingElement.style.display = 'none';
            alert('Unable to fetch articles at the moment. Please try again later.');
        });
}

function fetchMostPopular() {
    fetch(mostPopularUrl)
        .then(response => {
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            return response.json();
        })
        .then(data => {
            const articles = data.results;
            const mostPopularContainer = document.getElementById('most-popular-news');
            
            mostPopularContainer.innerHTML = ''; 

            articles.forEach(article => {
                const imgSrc = article.media && article.media.length > 0 
                    ? article.media[0]['media-metadata'][2].url
                    : 'https://via.placeholder.com/800x400';
                    
                const articleElement = document.createElement('article');
                articleElement.innerHTML = `
                    <img src="${imgSrc}" alt="News Image">
                    <h2>${article.title}</h2>
                    <p>${article.abstract}</p>
                    <a href="${article.url}" class="read-more">Read More....</a>
                `;
                mostPopularContainer.appendChild(articleElement);
            });
        })
        .catch(error => {
            console.error('Error fetching most popular articles:', error);
            alert('Unable to fetch most popular articles at the moment. Please try again later.');
        });
}

function fetchTopStories() {
    fetch(topStoriesUrl)
        .then(response => {
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`);
            }
            return response.json();
        })
        .then(data => {
            const articles = data.results;
            const topStoriesContainer = document.getElementById('top-stories-news');
            
            topStoriesContainer.innerHTML = ''; 

            articles.forEach(article => {
                const imgSrc = article.multimedia && article.multimedia.length > 0 
                    ? article.multimedia[0].url 
                    : 'https://via.placeholder.com/800x400'; 

                const articleElement = document.createElement('article');
                articleElement.innerHTML = `
                    <img src="${imgSrc}" alt="News Image">
                    <h2>${article.title}</h2>
                    <p>${article.abstract}</p>
                    <a href="${article.url}" class="read-more">Read More....</a>
                `;
                topStoriesContainer.appendChild(articleElement);
            });
        })
        .catch(error => {
            console.error('Error fetching top stories:', error);
            alert('Unable to fetch top stories at the moment. Please try again later.');
        });
}

fetchArticles(currentPage);
    fetchMostPopular();
    fetchTopStories();
});